import React from 'react';
import Axios from 'axios';
import Alert from 'react-s-alert';
import { RadioGroup, RadioButton } from 'react-radio-buttons';

export default class NewParent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            student: '',

            working: "",
            instagram: "",
            ownSmartphone: "",
            education: ""
        }
    }

    submitData() {
        if (!this.state.name) return Alert.warning("همه ی قسمت ها را پر کنید", { position: 'top-right', })
        if (!this.state.student) return Alert.warning("همه ی قسمت ها را پر کنید", { position: 'top-right', })
        if (!this.state.instagram) return Alert.warning("همه ی قسمت ها را پر کنید", { position: 'top-right', })
        if (!this.state.working) return Alert.warning("همه ی قسمت ها را پر کنید", { position: 'top-right', })
        if (!this.state.ownSmartphone) return Alert.warning("همه ی قسمت ها را پر کنید", { position: 'top-right', })
        if (!this.state.education) return Alert.warning("همه ی قسمت ها را پر کنید", { position: 'top-right', })

        Axios({
            method: "POST",
            url: global.apiHost + '/api/parent/add',
            data: {
                name: this.state.name,
                student: this.state.student,
                working: (this.state.working == 'true'),
                instagram: (this.state.instagram == 'true'),
                ownSmartphone: (this.state.ownSmartphone == 'true'),
                education: this.state.education
            }
        }).then((response) => {
            Alert.success(`${this.state.name} به لیست افزوده شد`)
            window.location.reload();
        }).catch((err) => {
            Alert.error("خطا", { position: 'top-right', })
        })
    }

    render() {
        return (
            <div>
                <div className="center-content" style={{ paddingBottom: 40 }} >
                    <div className="container" >
                        <h1 className="text-center F6 darkgray" style={{ marginTop: 20 }} >ثبت اطلاعات دانش آموزان</h1>
                        <p className="red text-center" >*در صورت نداشتن اطلاعات بخشی آن را با "ندارد" پر کنید*</p>
                        <div className="row" style={{ justifyContent: 'center', flexDirection: 'column', alignItems: 'center' }} >
                            <div className="col-12 col-md-6" style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', marginTop: 15 }}>
                                <label for="exampleInputEmail1" style={{ textAlign: 'right' }} >نام و نام خانوادگی</label>
                                <input value={this.state.name} onChange={(value) => this.setState({ name: value.target.value })} style={{ direction: 'rtl', textAlign: 'right' }} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="نام و نام خانوادگی" />
                            </div>

                            <div className="col-12 col-md-6" style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', marginTop: 15 }}>
                                <label for="exampleInputEmail1" style={{ textAlign: 'right' }} >نام و نام خانوادگی فرزند</label>
                                <input value={this.state.student} onChange={(value) => { this.setState({ student: value.target.value }) }} type="name" style={{ direction: 'rtl', textAlign: 'right' }} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="نام و نام خانوادگی فرزند" />
                            </div>

                            <div className="" style={{ marginTop: 20 }}>
                                <label>میزان تحصیلات</label>
                                <RadioGroup onChange={(value) => this.setState({ education: value })} horizontal>
                                    <RadioButton rootColor={'#616161'} pointColor="#458B00" value=" زیردیپلم">
                                        زیردیپلم
                                    </RadioButton>
                                    <RadioButton rootColor={'#616161'} pointColor="#458B00" value="دیپلم">
                                        دیپلم
                                    </RadioButton>
                                    <RadioButton rootColor={'#616161'} pointColor="#458B00" value="کاردانی">
                                        کاردانی
                                    </RadioButton>
                                    <RadioButton rootColor={'#616161'} pointColor="#458B00" value="کارشناسی">
                                        کارشناسی
                                    </RadioButton>
                                    <RadioButton rootColor={'#616161'} pointColor="#458B00" value="کارشناسی ارشد">
                                        کارشناسی ارشد
                                    </RadioButton>
                                    <RadioButton rootColor={'#616161'} pointColor="#458B00" value="دکتری">
                                        دکتری
                                    </RadioButton>
                                </RadioGroup>
                            </div>
                        </div>

                        <div className="center-content" style={{ marginTop: 20 }}>
                            <label>گوشی هوشمند</label>
                            <RadioGroup horizontal onChange={(value) => this.setState({ ownSmartphone: value })}>
                                <RadioButton rootColor={'#616161'} pointColor="#458B00" value={"false"}>
                                    ندارد
                                    </RadioButton>
                                <RadioButton rootColor={'#616161'} pointColor="#458B00" value={"true"}>
                                    دارد
                                    </RadioButton>
                            </RadioGroup>
                        </div>

                        <div className="center-content" style={{ marginTop: 20 }}>
                            <label>اینستاگرام</label>
                            <RadioGroup onChange={(value) => this.setState({ instagram: value })} horizontal>
                                <RadioButton rootColor={'#616161'} pointColor="#458B00" value={"false"}>
                                    ندارد
                                    </RadioButton>
                                <RadioButton rootColor={'#616161'} pointColor="#458B00" value={"true"}>
                                    دارد
                                    </RadioButton>
                            </RadioGroup>
                        </div>

                        <div className="center-content" style={{ marginTop: 20 }}>
                            <label>شاغل</label>
                            <RadioGroup onChange={(value) => this.setState({ working: value })} horizontal>
                                <RadioButton rootColor={'#616161'} pointColor="#458B00" value={"false"}>
                                    ندارد
                                    </RadioButton>
                                <RadioButton rootColor={'#616161'} pointColor="#458B00" value={"true"}>
                                    دارد
                                    </RadioButton>
                            </RadioGroup>
                        </div>
                        <div className="center-content" style={{ marginTop: 20 }} >
                            <button className="btn btn-primary" onClick={() => { this.submitData() }} >ثبت اطلاعات</button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


