import React from 'react';
import Axios from 'axios';
import Alert from 'react-s-alert';

export default class NewStudent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            food: '',
            teacher: "",
            fear: "",
            artist: "",
            place: "",
            pet: "",
            color: "",
        }
    }

    submitData() {
        if (!this.state.name) return Alert.warning("همه ی قسمت ها را پر کنید", { position: 'top-right', })
        if (!this.state.food) return Alert.warning("همه ی قسمت ها را پر کنید", { position: 'top-right', })
        if (!this.state.teacher) return Alert.warning("همه ی قسمت ها را پر کنید", { position: 'top-right', })
        if (!this.state.fear) return Alert.warning("همه ی قسمت ها را پر کنید", { position: 'top-right', })
        if (!this.state.artist) return Alert.warning("همه ی قسمت ها را پر کنید", { position: 'top-right', })
        if (!this.state.place) return Alert.warning("همه ی قسمت ها را پر کنید", { position: 'top-right', })
        if (!this.state.pet) return Alert.warning("همه ی قسمت ها را پر کنید", { position: 'top-right', })
        if (!this.state.color) return Alert.warning("همه ی قسمت ها را پر کنید", { position: 'top-right', })
        Axios({
            method: "POST",
            url: global.apiHost + '/api/student/add',
            data: {
                name: this.state.name,
                favoriteArtist: this.state.artist,
                favoriteTeacher: this.state.teacher,
                favoriteFood: this.state.food,
                favoriteColor: this.state.color,
                favoritePet: this.state.pet,
                favoritePlace: this.state.place,
                biggestFear: this.state.fear
            }
        }).then((response) => {
            Alert.success(`${this.state.name} به لیست افزوده شد`)
            this.setState({
                name: "",
                food: '',
                teacher: "",
                fear: "",
                artist: "",
                place: "",
                pet: "",
                color: "",
            })
        }).catch((err) => {

        })
    }

    render() {
        return (
            <div>
                <div className="center-content" style={{ paddingBottom: 40 }} >
                    <div className="container" >
                        <h1 className="text-center F6 darkgray" style={{ marginTop: 20 }} >ثبت اطلاعات دانش آموزان</h1>
                        <p className="red text-center" >*در صورت نداشتن اطلاعات بخشی آن را با "ندارد" پر کنید*</p>
                        <div className="row" style={{ justifyContent: 'center', flexDirection: 'column', alignItems: 'center' }} >
                            <div className="col-12 col-md-6" style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', marginTop: 15 }}>
                                <label for="exampleInputEmail1" style={{ textAlign: 'right' }} >نام و نام خانوادگی</label>
                                <input value={this.state.name} onChange={(value) => this.setState({ name: value.target.value })} style={{ direction: 'rtl', textAlign: 'right' }} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="نام و نام خانوادگی" />
                            </div>

                            <div className="col-12 col-md-6" style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', marginTop: 15 }}>
                                <label for="exampleInputEmail1" style={{ textAlign: 'right' }} >بزرگترین ترس</label>
                                <input value={this.state.fear} onChange={(value) => { this.setState({ fear: value.target.value }) }} type="name" style={{ direction: 'rtl', textAlign: 'right' }} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="بزرگترین ترس" />
                            </div>

                            <div className="col-12 col-md-6" style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', marginTop: 15 }}>
                                <label for="exampleInputEmail1" style={{ textAlign: 'right' }} >غذای مورد علاقه</label>
                                <input value={this.state.food} onChange={(value) => { this.setState({ food: value.target.value }) }} type="name" style={{ direction: 'rtl', textAlign: 'right' }} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="غذای مورد علاقه" />
                            </div>

                            <div className="col-12 col-md-6" style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', marginTop: 15 }}>
                                <label for="exampleInputEmail1" style={{ textAlign: 'right' }} >مکان تفریحی مورد علاقه</label>
                                <input value={this.state.place} onChange={(value) => { this.setState({ place: value.target.value }) }} type="name" style={{ direction: 'rtl', textAlign: 'right' }} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="مکان تفریحی مورد علاقه" />
                            </div>

                            <div className="col-12 col-md-6" style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', marginTop: 15 }}>
                                <label for="exampleInputEmail1" style={{ textAlign: 'right' }} >معلم مورد علاقه</label>
                                <input value={this.state.teacher} onChange={(value) => { this.setState({ teacher: value.target.value }) }} type="name" style={{ direction: 'rtl', textAlign: 'right' }} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="معلم مورد علاقه" />
                            </div>

                            <div className="col-12 col-md-6" style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', marginTop: 15 }}>
                                <label for="exampleInputEmail1" style={{ textAlign: 'right' }} >حیوان خانگی مورد علاقه</label>
                                <input value={this.state.pet} onChange={(value) => { this.setState({ pet: value.target.value }) }} type="name" style={{ direction: 'rtl', textAlign: 'right' }} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="حیوان خانگی مورد علاقه" />
                            </div>

                            <div className="col-12 col-md-6" style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', marginTop: 15 }}>
                                <label for="exampleInputEmail1" style={{ textAlign: 'right' }} >رنگ مورد علاقه</label>
                                <input value={this.state.color} onChange={(value) => { this.setState({ color: value.target.value }) }} type="name" style={{ direction: 'rtl', textAlign: 'right' }} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="رنگ مورد علاقه" />
                            </div>

                            <div className="col-12 col-md-6" style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', marginTop: 15 }}>
                                <label for="exampleInputEmail1" style={{ textAlign: 'right' }} >خواننده مورد علاقه</label>
                                <input value={this.state.artist} onChange={(value) => { this.setState({ artist: value.target.value }) }} type="name" style={{ direction: 'rtl', textAlign: 'right' }} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="خواننده مورد علاقه" />
                            </div>
                        </div>
                        <div className="center-content" style={{ marginTop: 20 }} >
                            <button className="btn btn-primary" onClick={() => { this.submitData() }} >ثبت اطلاعات</button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


