import React from 'react';
import Axios from 'axios';

export default class Students extends React.Component {
    constructor(props) {
        super(props);
        this.fetchParents();
        this.state = {
            parents: []
        }
    }

    fetchParents() {
        Axios({
            method: "POST",
            url: global.apiHost + '/api/student/list'
        }).then((response) => {
            console.log(response.data)
            this.setState({ parents: response.data.students });
        })
    }

    updateStudent(data) {
        Axios({
            method: 'POST',
            url: global.apiHost + '/api/student/update',
            data
        }).then(response => {
            console.log(response.data);
        }).catch(err => {
            console.log(err);
        })
    }

    renderRows() {
        return this.state.parents.map((item, index) => {
            return (
                <tr>
                    <th className="text-center" scope="row">{index + 1}</th>
                    <td className="text-center">{item.name}</td>
                    <td className="text-center col-style">
                        {item.favoriteColor}
                        <div>
                            <i onClick={() => {this.updateStudent({studentid: item._id, updates: {colorAnswered: true}})}} className="fas fa-check green" style={{ marginLeft: 5, color: 'green' }}></i>
                            <i onClick={() => {this.updateStudent({studentid: item._id, updates: {colorAnswered: false}})}} className="fas fa-times red" style={{ marginRight: 5 }}></i>
                        </div>
                    </td>
                    <td className="text-center">
                        {item.favoriteFood}
                        <div>
                            <i onClick={() => {this.updateStudent({studentid: item._id, updates: {foodAnswered: true}})}} className="fas fa-check green" style={{ marginLeft: 5, color: 'green' }}></i>
                            <i onClick={() => {this.updateStudent({studentid: item._id, updates: {foodAnswered: false}})}} className="fas fa-times red" style={{ marginRight: 5 }}></i>
                        </div>
                    </td>
                    <td className="text-center">
                        {item.favoriteTeacher}
                        <div>
                            <i onClick={() => {this.updateStudent({studentid: item._id, updates: {teacherAnswered: true}})}} className="fas fa-check green" style={{ marginLeft: 5, color: 'green' }}></i>
                            <i onClick={() => {this.updateStudent({studentid: item._id, updates: {teacherAnswered: false}})}} className="fas fa-times red" style={{ marginRight: 5 }}></i>
                        </div>
                    </td>
                    <td className="text-center">
                        {item.favoritePlace}
                        <div>
                            <i onClick={() => {this.updateStudent({studentid: item._id, updates: {placeAnswered: true}})}} className="fas fa-check green" style={{ marginLeft: 5, color: 'green' }}></i>
                            <i onClick={() => {this.updateStudent({studentid: item._id, updates: {placeAnswered: false}})}} className="fas fa-times red" style={{ marginRight: 5 }}></i>
                        </div>
                    </td>
                    <td className="text-center">
                        {item.favoriteArtist}
                        <div>
                            <i onClick={() => {this.updateStudent({studentid: item._id, updates: {artistAnswered: true}})}} className="fas fa-check green" style={{ marginLeft: 5, color: 'green' }}></i>
                            <i onClick={() => {this.updateStudent({studentid: item._id, updates: {artistAnswered: false}})}} className="fas fa-times red" style={{ marginRight: 5 }}></i>
                        </div>
                    </td>
                    <td className="text-center">
                        {item.favoritePet}
                        <div>
                            <i onClick={() => {this.updateStudent({studentid: item._id, updates: {petAnswered: true}})}} className="fas fa-check green" style={{ marginLeft: 5, color: 'green' }}></i>
                            <i onClick={() => {this.updateStudent({studentid: item._id, updates: {petAnswered: false}})}} className="fas fa-times red" style={{ marginRight: 5 }}></i>
                        </div>
                    </td>
                    <td className="text-center">
                        {item.biggestFear}
                        <div>
                            <i onClick={() => {this.updateStudent({studentid: item._id, updates: {fearAnswered: true}})}} className="fas fa-check green" style={{ marginLeft: 5, color: 'green' }}></i>
                            <i onClick={() => {this.updateStudent({studentid: item._id, updates: {fearAnswered: false}})}} className="fas fa-times red" style={{ marginRight: 5 }}></i>
                        </div>
                    </td>
                </tr>
            )
        })
    }

    render() {
        return (
            <div>
                <div>
                    <table class="table table-striped table-dark" style={{ direction: 'rtl' }}>
                        <thead>
                            <tr>
                                <th className="text-center" scope="col">#</th>
                                <th className="text-center" scope="col">نام</th>
                                <th className="text-center" scope="col">رنگ مورد علاقه</th>
                                <th className="text-center" scope="col">غذای مورد علاقه</th>
                                <th className="text-center" scope="col">معلم مورد علاقه</th>
                                <th className="text-center" scope="col">مکان مورد علاقه</th>
                                <th className="text-center" scope="col">خواننده مورد علاقه</th>
                                <th className="text-center" scope="col">حیوان خانگی مورد علاقه</th>
                                <th className="text-center" scope="col">بزرگترین ترس</th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.renderRows()}
                        </tbody>
                    </table>
                </div>
            </div>
        )
    }
}


