import React from 'react';
import Axios from 'axios';

export default class Parents extends React.Component {
    constructor(props) {
        super(props);
        this.fetchParents();
        this.state = {
            parents: []
        }
    }

    fetchParents() {
        Axios({
            method: "POST",
            url: global.apiHost + '/api/parent/list'
        }).then((response) => {
            console.log(response.data)
            this.setState({ parents: response.data.mothers });
        })
    }


    renderRows() {
        return this.state.parents.map((item, index) => {
            return (
                <tr>
                    <th className="text-center" scope="row">{index + 1}</th>
                    <td className="text-center">{item.name}</td>
                    <td className="text-center">{item.student}</td>
                    <td className="text-center">{item.ownSmarthphone ? 'دارد' : 'ندارد'}</td>
                    <td className="text-center">{item.ownSmarthphone ? 'دارد' : 'ندارد'}</td>
                    <td className="text-center">{item.education}</td>
                    <td className="text-center">{item.ownSmarthphone ? 'دارد' : 'ندارد'}</td>
                </tr>
            )
        })
    }

    render() {
        return (
            <div>
                <div>
                    <table class="table table-striped table-dark" style={{direction: 'rtl'}}>
                        <thead>
                            <tr>
                                <th className="text-center" scope="col">#</th>
                                <th className="text-center" scope="col">نام</th>
                                <th className="text-center" scope="col">نام فرزند</th>
                                <th className="text-center" scope="col">گوشی هئشمند</th>
                                <th className="text-center" scope="col">اینستاگرام</th>
                                <th className="text-center" scope="col">مدرک تحصیلی</th>
                                <th className="text-center" scope="col">شاغل</th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.renderRows()}
                        </tbody>
                    </table>
                </div>
            </div>
        )
    }
}


