import React from "react";
import ReactDOM from "react-dom";
import registerServiceWorker from "./registerServiceWorker";
import { Switch, Route, BrowserRouter } from "react-router-dom";
import Alert from 'react-s-alert';
 
import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/slide.css';
//CSS
import "./index.css";

//Containers
import Students from "./containers/students";
import Parents from "./containers/parents";
import NewParent from './containers/newParent';
import NewStudent from './containers/newStudent';

global.apiHost = ''

const routes = (
  <div>
    <Alert stack={{limit: 3}} />
    <BrowserRouter>
      <Switch>
        <Route exact path="/students" component={Students} />
        <Route exact path="/new-student" component={NewStudent} />
        <Route exact path="/new-parent" component={NewParent} />
        <Route exact path="/parents" component={Parents} />
      </Switch>
    </BrowserRouter>
  </div>
);
ReactDOM.render(routes, document.getElementById("root"));
registerServiceWorker();



